import React, { Component, Fragment } from "react";
import {Link} from 'react-router-dom'
import axios from "axios";
import ProductosEcommerce from './ProductosEcommerce'

export default class RubrosEcommerce extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      search: '',
      value: '',
    };

    this.handleClick = this.handleClick.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

  }

  receivedRubroGroup() {
    axios.get(`https://www.sundelimpieza.com/api/rubros/all`).then((res) => {
      const data = res.data;
      const postRubroGroup = data.map((pd) => (
        <React.Fragment>
          <div>
              <div class="uk-card uk-card-default">
                  <div class="uk-card-media-top">
                    <img
                      src={"https://www.sundelimpieza.com/static/images/" + pd.Foto}
                      alt="Producto SundeLimpieza"
                      className="uk-max-width uk-padding-small"
                    />
                  </div>
                  <div class="uk-card-body">
                    <h5>{pd.SubRubro}</h5>
                    <button class="uk-button uk-button-primary uk-button-small" onClick={this.handleClick} id={pd.SubRubro}>Ver ms</button>
                  </div>
              </div>
          </div>
        </React.Fragment>
      ));

      this.setState({
        postRubroGroup,
      });
    });
  }

  receivedRubro() {
    axios.get(`https://www.sundelimpieza.com/api/productos/subrubro`).then((res) => {
      const data = res.data;
      const postRubro = data.map((pd) => (
        <React.Fragment>
          <div className="uk-padding-list">
            <Link to="#" onClick={this.handleClick}>
              <li className="uk-text-capitalize uk-text-break uk-text-small" id={pd._id.SubRubro}>{pd._id.SubRubro}</li>
            </Link>
          </div>
        </React.Fragment>
      ));

      this.setState({
        postRubro,
      });
    });
  }

  handleClick(e) {
    this.setState({ value: e.target.getAttribute('id') });
  }

  handleChange(e) {
    this.setState({ search: e.target.value });
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.setState({ value: this.state.search });
  };

  componentDidMount() {
    this.receivedRubro();
    this.receivedRubroGroup();
  }

  render() {
    return (
      <div className="uk-container uk-margin uk-padding-bottom">
            {
              this.state.value ?
                <ProductosEcommerce SubRubro={this.state.value}/>   
              : 
                <Fragment>
                <div className="uk-text-center" uk-grid="true">
                      <div className="uk-width-expand@m">
                        <div className="uk-child-width-1-3@s uk-child-width-1-4@m uk-grid-small" uk-grid="masonry: true">
                          {this.state.postRubroGroup}
                        </div>
                      </div>
                      <div className="uk-width-1-4@m">
                          <div className="uk-card uk-card-default uk-card-body">
                            <h5 className="uk-text-bolder uk-text-center uk-margin-top">Buscador de Producto:</h5>
                            <form class="uk-search uk-search-small" onSubmit={this.handleSubmit}>
                              <input class="uk-search-input" type="search" placeholder="...Ingresar Busqueda...." onChange={this.handleChange}/>
                            </form>
                            <hr class="uk-divider-icon"></hr>
                            <h5 className="uk-text-bolder uk-text-center uk-margin-top">Listado de Rubros:</h5>
                            <ul className="uk-list uk-text-center">
                              {this.state.postRubro}
                            </ul>
                          </div>
                      </div>
                  </div>
                </Fragment>
            }
        
      </div>
    );
  }
}
