import React, { Fragment } from "react";
import { Link } from "react-router-dom";

const NavegationEcommerce = (props) => {
  return (
    <Fragment>
    
      <nav className="uk-navbar-container" uk-navbar="true">
        <div className="uk-navbar-left">
          <Link class="uk-logo uk-logo uk-margin-large-left" to="#"><img src="static/images/logo.png" alt="Sunde Limpieza" width="170" className="uk-padding-small" /></Link>
        </div>

        <div className="uk-navbar-right">
          <ul className="uk-navbar-nav uk-logo uk-margin-large-right">
            <li className="uk-text-bold">
              <Link to="#">Productos</Link>
            </li>
            <li className="uk-text-bold">
              <Link to="#footer" uk-scroll="true">Contactanos</Link>
            </li>
          </ul>
        </div>
      </nav>
      <div class="uk-position-relative uk-visible-toggle uk-light" uk-slideshow="true; min-height: 200; max-height: 200">

          <ul class="uk-slideshow-items">
              <li>
                  <img src="static/images/slider01.jpg" alt="Sunde Limpieza" uk-cover="true"/>
              </li>
              <li>
                  <img src="static/images/slider02.jpg" alt="Suned Limpieza" uk-cover="true"/>
              </li>
              <li>
                  <img src="static/images/slider03.jpg" alt="Suned Limpieza" uk-cover="true"/>
              </li>
              <li>
                  <img src="static/images/slider04.jpg" alt="Suned Limpieza" uk-cover="true"/>
              </li>
          </ul>

          <Link class="uk-position-center-left uk-position-small uk-hidden-hover" to="#" uk-slidenav-previous="true" uk-slideshow-item="previous"></Link>
          <Link class="uk-position-center-right uk-position-small uk-hidden-hover" to="#" uk-slidenav-next="true" uk-slideshow-item="next"></Link>

      </div>
    </Fragment>
  );
};

export default NavegationEcommerce;
