import React, { Component, Fragment } from "react";
import axios from "axios";
import { Link } from 'react-router-dom';

import ProductosEcommerceMenu from './ProductosEcommerceMenu'

export default class ProductoDetail extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      detail: this.props.Producto,
      count: 0,
      link: '',
      search: '',
    };

    this.handleClick = this.handleClick.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

  }

  receivedProductoDetail() {
    axios.get(`https://www.sundelimpieza.com/api/productos/detalle/${this.state.detail}`).then((res) => {
      const data = res.data;
      const postProductoDetail = data.map((pd) => (
        <React.Fragment>
            <div>
              <div className="uk-card uk-card-default uk-card-small uk-card-body uk-text-center">
                <img
                  src={"https://www.sundelimpieza.com/static/images/" + pd.Foto}
                  alt="Producto SundeLimpieza"
                  className="uk-max-width"
                />
                <h5>{pd.Producto}</h5>
                <p className="uk-card-title">{pd.Detalle}</p>
              </div>
            </div>
        </React.Fragment>
      ));
      this.setState({
        postProductoDetail,
      });
    });
  }

  receivedRubro() {
    axios.get(`https://www.sundelimpieza.com/api/productos/subrubro`).then((res) => {
      const data = res.data;
      const postRubro = data.map((pd) => (
        <React.Fragment>
          <div className="uk-padding-list">
            <Link to="#" onClick={this.handleClick}>
              <li className="uk-text-capitalize uk-text-break uk-text-small" id={pd._id.SubRubro}>{pd._id.SubRubro}</li>
            </Link>
          </div>
        </React.Fragment>
      ));

      this.setState({
        postRubro,
      });
    });
  }

  handleClick(e) {
    this.setState({ link: e.target.getAttribute('id') });
  }

  handleChange(e) {
    this.setState({ search: e.target.value });
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.setState({ link: this.state.search });
  };
  
  componentDidMount() {
    this.receivedRubro();
    this.receivedProductoDetail();
  }

  render() {
    return (
      <div className="uk-container">

        {
          this.state.link ?
            <ProductosEcommerceMenu Menu={this.state.link} />
          :
            <Fragment>
              <div className="uk-text-center" uk-grid="true">
                  <div className="uk-width-expand@m">
                    <h4 className="uk-text-bolder uk-text-left uk-margin-top">Detalle del Producto:</h4>
                    <div className="uk-child-width-1-3@s uk-child-width-1-4@m uk-grid-small" uk-grid="masonry: true">
                      {this.state.postProductoDetail}
                    </div>
                  </div>
                  <div className="uk-width-1-4@m">
                      <div className="uk-card uk-card-default uk-card-body">
                        <h5 className="uk-text-bolder uk-text-center uk-margin-top">Buscador de Producto:</h5>
                        <form class="uk-search uk-search-small" onSubmit={this.handleSubmit}>
                          <input class="uk-search-input" type="search" placeholder="...Ingresar Busqueda...." onChange={this.handleChange}/>
                        </form>
                        <hr class="uk-divider-icon"></hr>
                        <h5 className="uk-text-bolder uk-text-center uk-margin-top">Listado de Rubros:</h5>
                        <ul className="uk-list uk-text-center">
                          {this.state.postRubro}
                        </ul>
                      </div>
                  </div>
              </div>
          </Fragment>
        }
      </div>
    );
  }
}
